
"use client";

import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useAuth } from "@/hooks/use-auth";
import { Loader2, UserCog } from "lucide-react";
import { useToast } from "@/hooks/use-toast";

const profileFormSchema = z.object({
  name: z.string().min(2, { message: "Nama minimal 2 karakter." }).optional(),
  // email: z.string().email({ message: "Alamat email tidak valid." }), // Email usually not changed by user directly
});

// Optional: Password change schema, can be a separate form
// const passwordFormSchema = z.object({
//   currentPassword: z.string().min(6, { message: "Kata sandi saat ini minimal 6 karakter." }),
//   newPassword: z.string().min(6, { message: "Kata sandi baru minimal 6 karakter." }),
//   confirmNewPassword: z.string().min(6, { message: "Konfirmasi kata sandi baru minimal 6 karakter." }),
// }).refine((data) => data.newPassword === data.confirmNewPassword, {
//   message: "Kata sandi baru dan konfirmasi tidak cocok.",
//   path: ["confirmNewPassword"],
// });

export default function SettingsPage() {
  const { user, updateUserProfile, isLoading: authLoading } = useAuth();
  const { toast } = useToast();
  const [pageLoading, setPageLoading] = React.useState(false);

  const profileForm = useForm<z.infer<typeof profileFormSchema>>({
    resolver: zodResolver(profileFormSchema),
    defaultValues: {
      name: user?.name || "",
      // email: user?.email || "",
    },
  });

  useEffect(() => {
    if (user) {
      profileForm.reset({
        name: user.name || "",
        // email: user.email,
      });
    }
  }, [user, profileForm]);

  async function onProfileSubmit(values: z.infer<typeof profileFormSchema>) {
    if (!user) return;
    setPageLoading(true);
    const success = await updateUserProfile(user.id, { name: values.name });
    if (success) {
      toast({
        title: "Profil Diperbarui",
        description: "Informasi profil Anda telah berhasil disimpan.",
      });
    } else {
      toast({
        title: "Pembaruan Gagal",
        description: "Tidak dapat memperbarui profil Anda saat ini.",
        variant: "destructive",
      });
    }
    setPageLoading(false);
  }

  // Placeholder for password change submission
  // async function onPasswordSubmit(values: z.infer<typeof passwordFormSchema>) {
  //   toast({ title: "Info", description: "Fungsi ubah kata sandi belum diimplementasikan." });
  // }

  if (authLoading || !user) {
    return (
      <div className="flex h-full items-center justify-center">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-headline font-semibold">Pengaturan Akun</h1>
        <p className="text-muted-foreground">Kelola informasi profil dan pengaturan akun Anda.</p>
      </div>

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="flex items-center">
            <UserCog className="mr-2 h-6 w-6 text-primary" />
            Informasi Profil
          </CardTitle>
          <CardDescription>Perbarui nama dan detail pribadi Anda.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...profileForm}>
            <form onSubmit={profileForm.handleSubmit(onProfileSubmit)} className="space-y-6">
              <FormField
                control={profileForm.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Nama Lengkap</FormLabel>
                    <FormControl>
                      <Input placeholder="Nama lengkap Anda" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                name="email" // Not part of schema, just for display
                render={() => (
                  <FormItem>
                    <FormLabel>Email</FormLabel>
                    <FormControl>
                      <Input value={user.email} disabled className="bg-muted/50"/>
                    </FormControl>
                    <p className="text-xs text-muted-foreground">Email tidak dapat diubah.</p>
                  </FormItem>
                )}
              />
              
              <Button type="submit" disabled={pageLoading || authLoading}>
                {(pageLoading || authLoading) && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Simpan Perubahan Profil
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>

      {/* Placeholder for Password Change Section - Can be enabled later
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="flex items-center">
            <LockKeyhole className="mr-2 h-6 w-6 text-primary" />
            Ubah Kata Sandi
          </CardTitle>
          <CardDescription>Perbarui kata sandi akun Anda secara berkala untuk keamanan.</CardDescription>
        </CardHeader>
        <CardContent>
          <p className="text-sm text-muted-foreground">Fitur ubah kata sandi akan tersedia di sini.</p>
          {/* 
          <Form {...passwordForm}> // Assuming passwordForm is initialized
            <form onSubmit={passwordForm.handleSubmit(onPasswordSubmit)} className="space-y-6">
              // Fields for currentPassword, newPassword, confirmNewPassword
              <Button type="submit" disabled={pageLoading || authLoading}>
                Ubah Kata Sandi
              </Button>
            </form>
          </Form>
          * /}
        </CardContent>
      </Card>
      */}
    </div>
  );
}
